<template>
  <auth-layout>

    <form class="auth-form" @submit.prevent='confirm()'>

      <div class="material-icons-outlined auth-icon">restart_alt</div>
      <h2>Зміна пароля</h2>

      <div v-if="!this.$route.query.link">
        <p>Вкажіть скриньку, на яку ви реєстрували акаунт. На неї надійде лист з посиланням для зміни пароля</p>
        <input type="text" 
         class="input-textfield-line" 
         :class='{ "input-textfield-invalid": v$.email.$error }' 
         v-model.trim='email' 
         placeholder='Ваш email'
        >
        <div v-if='v$.email.$error'>
          <div class='input-error-message' v-if='v$.email.required.$invalid'>Поле має бути заповнене</div>
          <div class='input-error-message' v-if='v$.email.email.$invalid'>Некоректний формат</div>
        </div>
        <button type='submit' class='button-plain'>Надіслати</button>
      </div>

      <div v-else>
        <input type="password" 
         class="input-textfield-line" 
         :class='{ "input-textfield-invalid": v$.password.$error }' 
         v-model.trim='password' 
         maxlength=32
         placeholder='Ваш пароль'
        >
        <div v-if='v$.password.$error'>
          <div class='input-error-message' v-if='v$.password.required.$invalid'>Поле має бути заповнене</div>
          <div class='input-error-message' v-if='v$.password.minLength.$invalid'>Пароль має бути довшим за {{v$.password.minLength.$params.min}} символів</div>
        </div>
        <input type="password" 
         class="input-textfield-line" 
         :class='{ "input-textfield-invalid": v$.repassword.$error }' 
         v-model.trim='repassword' 
         maxlength=32
         placeholder='Пароль ще раз'
        >
        <div class='input-error-message' v-if='v$.repassword.$error'>Паролі мають співпадати</div>
        <button type='submit' class='button-plain'>Змінити пароль</button>
      </div>

      <small><router-link to='/auth'>Назад до авторизації</router-link></small>

    </form>

  </auth-layout>
</template>

<script>
import useVuelidate from '@vuelidate/core'
import { required, email, minLength, sameAs } from '@vuelidate/validators'
import controller from '../api/controller'
import authLayout from '@/layouts/auth-layout.vue'
export default {
  components: { authLayout },
  data() { return {
    v$: useVuelidate(),
    email: '',
    password: '',
    repassword: '',
    activationLink: this.$route.query.link
  }},
  validations() { return {
    email: { required, email },
    password: { required, minLength: minLength(6) },
    repassword: { sameAs: sameAs(this.password) }
  }},
  methods:  {
    async confirm() {
      this.v$.$touch()
      if (!this.v$.email.$error) await controller.requestPasswordRenewal(this.email)
      if (!this.v$.password.$error && !this.v$.repassword.$error) await controller.renewPassword(this.password, this.activationLink)
    }
  }
}
</script>

<style lang="scss" scoped>

</style>